@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,600;0,700;0,800;1,400;1,600;1,700;1,800&display=swap);
.header-container {
    background-color: var(--primary-color);
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-container span:first-child {
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.header-container span {
    color: #f1f1f1;
    
}

.header-container div  {
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.header-container div span  {
    cursor: pointer;
}


@media screen and (min-width: 600px) {
    .header-container span:first-child {
        width: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        
    }
}



/* CSS das notificações */
.container-notification p {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: red;
    box-shadow: 1px 1px 3px black;
    color: #f1f1f1;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.d-none {
    display: none;
}
.main-container {
    background-color: var(--alternative-color);
    min-height: calc(100vh - 90px);
    padding: 0;
}

/* DESK VIEW */
@media screen and (min-width: 600px) {
    .main-container {
        display: flex;
        min-width: 100%;
    }


}
.footer-container {
    background-color: #3d3d3d;
    color: #f1f1f1;
}
/* CSS DO CONTAINER FRIENDLIST */
.friendlist-container {
    background-color: aliceblue;
    box-shadow: 0px 1px 3px black;
    display: flex;
    padding: 0;
    align-items: center;
    overflow: auto;
}

.friendlist-container-busca {
    
}

@media screen and (min-width: 600px) {
    .friendlist-container {
        max-width: 20%;
        box-shadow: unset;
        display: block;
        margin: 0;
        box-shadow: 1px 0px 3px black;
        padding: 0;
    }
}


/* CSS DO CONTAINER FRIENDICON */
.friendicon-container {
    width: 100px;
    height: 90px;
    margin-right: 5px;
}
.friendicon-container:hover {
    background-color: var(--secundary-color);
}

.friendicon-image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    margin: 5px auto;
    border-radius: 50%;
    background-color: #c0c0c0;
    color: #f1f1f1;
    text-shadow: 0px 0px 2px black;
    font-weight: bold;
    font-size: var(--font-xl)
}

.friendicon-label {
    font-size: var(--font-xs);
    text-align: center;
}

@media screen and (min-width: 600px) {
    .friendicon-container {
        display: flex;
        width: 100%;
        height: 65px;
        align-items: center;
        margin: 0 auto;
        padding: 5px;
        cursor: pointer;
    }
    
    .friendicon-image {
        margin: 5px;
    }

    .friendicon-label {
        min-width: calc(100% - 60px);
    }
}

/* CSS do friendicon no motor de busca */
.friendicon-container-busca {
    display: flex;
    width: 100%;
    height: 65px;
    align-items: center;
    margin: 0 auto;
    padding: 5px;
    cursor: pointer;
}

.friendicon-container-busca .friendicon-image {
    margin: 5px;
}

.friendicon-container-busca .friendicon-label {
    min-width: calc(100% - 60px);
}
/* formularios da homepage */
.container-form-homepage {
    position: absolute;
    z-index: 1;
    background-color: #f1f1f1;
    padding: 15px 5px;
    border-radius: 14px;
}

.container-form-homepage span {
    display: inline-block;
    width: 100%;
    padding: 10px;
    text-align: right;
    cursor: pointer;
}

/* formulario de post do feed */
.formpost-container {
    padding-top: 10px;
}

@media screen and (min-width: 600px) {
    .formpost-container {
        max-width: 100%;
        max-height: 180px;
        margin: 0;
    }
}


/* formulario do motor de busca de usuarios */


/* formulario de dados do usuario da pagina perfil */
.container-perfil-form {
    margin: 5px;
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
}

.container-perfil-form div:first-child {
    display: flex;
    justify-content: space-between;
}

.container-perfil-form div:first-child span {
    cursor: pointer;
    width: 80px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.container-perfil-form div:first-child span:hover {
    background-color: var(--secundary-color);
    color: #f1f1f1;
}
.menu-big-container {
    position: fixed;
    display: flex;
    z-index: 1;
    width: 100vw;
    height: 100vh;
    top: 0;
    right: -100%;
    transition: 0.5s;
}

.menu-big-container aside:first-child {
    width: 30%;
    height: 100%;
}

.aside-menu {
    width: 70%;
    height: 100%;
    background-color: #fff;
    box-shadow: -1px 0px 3px black;
    
}

.show-aside {
    right: 0;
}

@media screen and (min-width: 600px) {
    .menu-big-container aside:first-child {
        width: 75%;
    }

    .aside-menu {
        width: 25%;
    }

    .aside-menu p {
        height: 40px;
        padding: 5px;
        margin: 0;
        cursor: pointer;
    }

    .aside-menu p:hover {
        background-color: var(--primary-color);
        color: #fff;
    }
}
.homepage-container {
    padding: 1px;
}

.homepage-container p {
    width: 80%;
    margin: 5px auto;
}

.homepage-container p:nth-child(1) {
    margin: 26px auto 0px auto;
    font-size: var(--font-l);
    color: #4b4b4b;
    font-weight: bolder;
}

.homepage-container p:nth-child(2) {
    text-align: right;
    font-size: var(--font-n);
    color: #4b4b4b;
}

.homepage-container section {
    min-height: 400px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.homepage-container .btn {
    font-size: var(--font-xl);
    font-weight: 600;
}

@media screen and (min-width: 600px) {
    .homepage-container {
        margin: auto;
    }
}
/* CSS do container de cada publicação */
.pub-container {
    width: 90%;
    margin: 5px auto;
}

.pub-container div {
    display: flex;
    align-items: center;
    padding: 5px;
}
.pub-image-position {
    margin: unset;
}
.pub-container div label {
    width: 80%;
    margin-left: 10px;
}

@media screen and (min-width: 600px) {

}

.feed-container {
    padding: 0;
    padding-bottom: 5px;
    background-color: #f2f2f2;
}

/* DESK VIEW */
@media screen and (min-width: 600px) {
    .feed-container {
        min-width: 100%;
        display: flex;

    }
    .desk-view-pubs {
        width: 55%;
    }
}
.container-perfil {
    padding: 1px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;

    --primary-color: #295872;
    --primary-shadow: #357599;
    --secundary-color: #4292bf;
    --secundary-shadow: #67a8cd;
    --alternative-color: #8dbeda;

    --font-xs: 12px;
    --font-s: 16px;
    --font-n: 18px;
    --font-l: 22px;
    --font-xl: 28px;
}

a {
    text-decoration: none;
    color: unset;
    
}

